body {
	font: 12pt Arial;
	padding: 0;
	margin : 0;
	background: url(images/Blue.jpg);
}

canvas {
  width: 1200px;
  height: 900px;
  padding-left: 0;
  padding-right: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid black;
}

#message {
  position: absolute;
  top: 20px;
  left: 20px;
  width: 500px;
  height: 500px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

#breakout {
	position: relative;
}

#levels{
	position: absolute;
	left: 10px;
	top: 10px;
	color: #666;
	line-height: 20px;
}


